.wpo-breadcumb-area {
  background: url(../../images/batticaloa-1920x700.jpg) no-repeat center top /
    cover;
  min-height: 400px;
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  z-index: 1;

  @media (max-width: 767px) {
    min-height: 250px;
  }

  &:before {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    //background: $dark-gray;
    content: '';
    z-index: -1;
    opacity: 0.7;
  }
  .wpo-breadcumb-wrap {
    text-align: center;

    h2 {
      font-size: 60px;
      color: #fff;
      line-height: 70px;
      margin-top: -10px;
      margin-bottom: 20px;

      @media (max-width: 767px) {
        font-size: 30px;
        line-height: 35px;
        margin-bottom: 10px;
      }
    }

    ul {
      li {
        display: inline-block;
        padding: 0px 5px;
        color: #fff;
        position: relative;
        font-size: 20px;
        font-family: $heading-font;

        @media (max-width: 767px) {
          font-size: 18px;
        }

        &:after {
          content: '/';
          position: relative;
          left: 7px;
        }

        &:last-child {
          span {
            color: #cbd4fd;
          }
          &:after {
            display: none;
          }
        }

        a {
          color: #fff;
          font-size: 20px;
          transition: all 0.3s;

          &:hover {
            color: $theme-primary-color;
          }
        }
      }
    }
  }
}

.wpo-about-section.wpo-about-section-s2 {
  padding-top: 170px;
}
