.theme-btn,
.theme-btn-s2 {
  background: $theme-primary-color;
  @include linear-gradient-bg(
    to left,
    transparentize($theme-primary-color, 0.25),
    darken($theme-primary-color, 9%)
  );
  color: $white;
  display: inline-block;
  font-weight: 500;
  padding: 10px 25px;
  border: 0;
  border-radius: 6px;
  text-transform: capitalize;
  -webkit-backface-visibility: hidden;
  z-index: 1;
  position: relative;
  font-size: 20px;
  font-size: calc-rem-value(20);

  &:after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 6px;
    @include linear-gradient-bg(
      to left,
      darken($theme-primary-color, 9%),
      transparentize($theme-primary-color, 0.25)
    );
    transition: opacity 0.5s ease-out;
    opacity: 0;
    z-index: -1;
  }

  &:hover,
  &:focus,
  &:active {
    //background: darken($theme-primary-color, 5%);
    //@include linear-gradient-bg(left, #00ded8, #00a7d5);
    color: $white;
  }

  &:hover:after {
    opacity: 1;
  }

  @include media-query(991px) {
    font-size: 18px;
    font-size: calc-rem-value(18);
  }

  @include media-query(767px) {
    padding: 12px 18px;
    font-size: 14px;
  }
}

.theme-btn-s2 {
  @include linear-gradient-bg(
    to left,
    transparentize($theme-primary-color-s2, 0.25),
    darken($theme-primary-color-s2, 19%)
  );

  &:after {
    @include linear-gradient-bg(
      to left,
      darken($theme-primary-color-s2, 9%),
      transparentize($theme-primary-color-s2, 0.25)
    );
  }

  @include media-query(991px) {
    font-size: 14px;
    font-size: calc-rem-value(14);
  }

  &:hover,
  &:focus,
  &:active {
    // background: darken($theme-primary-color, 5%);
    // @include linear-gradient-bg(left, #00ded8, #00a7d5);
    color: $white;
  }

  &:hover:after {
    opacity: 1;
  }

  @include media-query(767px) {
    padding: 12px 18px;
    font-size: 13px;
  }
}

.theme-btn-s3 {
  @extend .theme-btn;
  background: #c968ff;
  box-shadow: 0 0 0 5px #f3e0fe;
  @include linear-gradient-bg(left, #6168f3, #c968ff);
  z-index: 1;
  position: relative;

  &:after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 55px;
    @include linear-gradient-bg(left, #c968ff, #6168f3);
    transition: opacity 0.5s ease-out;
    opacity: 0;
    z-index: -1;
  }

  &:hover:after {
    opacity: 1;
  }

  &:hover,
  &:focus,
  &:active {
    color: $white;
  }
}

.theme-btn-s4 {
  @extend .theme-btn-s3;
  background: #f6d365;
  @include linear-gradient-bg(left, #f6d365, #fda085);
  box-shadow: 0 0 0 5px #fcf5df;

  z-index: 1;
  position: relative;

  &:after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 55px;
    @include linear-gradient-bg(left, #fda085, #f6d365);
    transition: opacity 0.5s ease-out;
    opacity: 0;
    z-index: -1;
  }

  &:hover:after {
    opacity: 1;
  }

  &:hover,
  &:focus,
  &:active {
    // background: #fda085;
    // @include linear-gradient-bg(top, #fda085, #f6d365);
    color: $white;
  }
}

.view-cart-btn {
  @extend .theme-btn;
  display: block;
  margin-top: 20px;
}
